.Overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
}

.Modal {
  max-width: calc(100vw - 48px);
  max-height: calc(100vh - 24px);
}

.block {
  background: white;
  padding: 20px;
}

.title {
  margin: 0 auto;
  font-size: 20px;
  word-wrap: break-word;
}

.description {
  word-wrap: break-word;
}
